body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

.encryptor-button {
  background-color: DodgerBlue;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 24px;
  float: left;
  width: 50%;
}

.encryptor {
  background: DodgerBlue;
  color: white;
  float: left;
  width: 100%;
}

h1.encryptor {
  color: white;
  font-size: 18px;
  font-weight: normal;
  float: none;
}

.decryptor-button {
  background-color: Grey;
  border: none;
  color: white;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 24px;
  width: 50%;
}

.decryptor {
  background: Grey;
  color: white;
  float: left;
  width: 100%;
}

h1.decryptor {
  color: white;
  font-size: 18px;
  font-weight: normal;
  float: none;
}

.hidden {
  display:none;
}



